@import 'reset';
// @import 'mixins';
@import 'vars';
@import 'generated/sprite-svg';
@import 'animated';
@import 'libs/slick';
@import 'libs/slick-theme';
@import 'libs/magnific-popup';
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
img {
  max-width: 100%;
}
:root {
  font-family: $font-primary;
  line-height: normal;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

.wrapper {
  width: 100%;
  overflow: hidden;
  font-family: $font-primary;
  color: #fff;
  font-size: 15px;
  line-height: 1.3;
  .icon {
    fill: currentColor;
  }
}

/* text-based popup styling */
.white-popup {
  position: relative;
  background: #fff;
  padding: 25px;
  width: auto;
  max-width: 400px;
  margin: 0 auto;
  .columns {
    font-size: 14px;
  }
}
a {
  color: inherit;
}
picture {
  source,
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.bg-absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center bottom;
  }
}
.circle {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  overflow: hidden;
  p {
    max-width: 70%;
  }
}
img {
  border: 0;
}

img:not([src]):not([srcset]) {
  visibility: hidden;
}

.lazyload {
  animation-name: fade;
  animation-duration: 1s;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.icon {
  font-size: 20px;
}
.slick-initialized .slick-slide {
  height: 100%!important;
  padding: 0;
}
// Artboard
.artboard {
  display: flex;
  position: relative;
  min-height: 100vh;
}
// Artboard Person
.artboard--person {
  background-color: #522e91;

  .artboard__inner {
    padding: 100px 70px 50px 60px;
  }
  .technical-ref {
    font-family: 'Helvetica', $font-primary;
    font-size: 8px;
    font-weight: 300;
    &.bottom {
      margin-top: -11px;
    }
    &.right {
      position: absolute;
      right: -65px;
      bottom: 118px;
      transform: rotate(-90deg);
    }
  }
}
.artboard__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  z-index: 1;
  max-width: 630px;
  width: 100%;
  margin: 0 auto;
}
.person__pic {
  width: 360px;
  height: 360px;
  margin: 0 auto;
}
.textblock1 {
  margin-top: 60px;
  align-self: flex-end;
  max-width: 185px;
  font-weight: 300;

  .icon-reddys {
    font-size: 39.5px;
  }
  .link {
    font-size: 12px;
  }
}
.textblock1__drreddys {
  margin: 32px 0 16px;
}
.text-bold1 {
  margin-top: 4px;
  font-weight: 900;
  font-size: 22px;
  font-family: $font-heading;
  line-height: 1;
}
// Artboard Case
.artboard--case {
  .artboard__inner {
    padding: 210px 55px 40px 70px;
  }
}
.textblock2 {
  .circle.xs {
    margin: 34px 0 32px -15px;
  }
}
.textblock2__capting {
  max-width: 284px;
  font-size: 36px;
  font-weight: bold;
  line-height: 1;
}
.drreddys-icon {
  margin: auto 0 0 auto;
  .icon {
    font-size: 60px;
  }
}
// Artboard bubles
.artboard--bubles {
  .artboard__inner {
    padding: 70px;
  }
}

// Artboard description
.artboard--description {
  color: #6d6e71;
  background-color: #fff;
  .artboard__inner {
    padding: 100px 45px 50px 45px;
  }
  .columns {
    max-width: 410px;
  }
  .drreddys-icon {
    margin-top: 10px;
    color: #522e91;
    .icon {
      font-size: 30px;
    }
  }
}
.column__title {
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
}
.columns {
  display: flex;
  font-size: 12px;
  font-weight: 300;
  p {
    margin-bottom: 10px;
  }
  .c-purple {
    font-weight: 400;
  }
  .column:not(:last-child) {
    .column__content {
      border-right: 1px solid #6d6e71;
    }
  }
}

.column__head {
  padding: 0 10px;
  color: #fff;
  .circle {
    width: 54px;
    height: 54px;
    font-size: 36px;
  }
}
.column__content {
  margin-top: 16px;
  padding: 0 10px;
}
.c-blue {
  color: #00b7f1;
}
.c-orange {
  color: #f37043;
}
.c-purple {
  color: #522e91;
}
.circle {
  &.xs {
    width: 153px;
    height: 153px;
  }
  &.md {
    width: 216px;
    height: 216px;
  }
  &.lg {
    width: 240px;
    height: 240px;
  }
  &.regular {
    width: 240px;
    height: 240px;
  }
  &.orange {
    background-color: #f37043;
  }
  &.blue {
    background-color: #00b7f1;
  }
  &.purple {
    background-color: #522e91;
  }
}
